import { Button } from "react-bootstrap";
import { FontAwesomeIcon } from "../../../../../CommonComponents/AssetsHelper";

    const ConnectButton = (props) => {

        return ![29].includes(props.network) ? (
        <>
        <div className="conn_status_wrap text-end">
            <Button variant="primary" onClick={() => window.open(props.link)} type="button" className={`btn-min-w text-nowrap ${props.btnClass}`}>
                <FontAwesomeIcon type={'brands'} icon={'rocketchat'} iconClass={'text-white fs-6 me-1'} /> Connect
            </Button>
        </div>
        </>
    ) : null;
    };

    export default ConnectButton;
