import React, { Suspense, useState } from "react";
import { Button, Card, CardBody, CardHeader, CardTitle, Table } from "react-bootstrap";
import ConnectButton from "../../layout/common/connectButton";
import AuthorInfo from "./authorInfo";
import NoData from "../../layout/common/noData";
import { getwallRecordMostActiveUsersMore } from "../../../../../../actions/analyticsAction";
import SectionHeading from "../../layout/common/sectionHeading";
import MultiPostMedia from "../../layout/common/multiPostMedia";
import PopupModal from "../../layout/common/popup/popupModal";
import { getwallRecordMostActiveUsersMoreDashboad } from "../../../../../../actions/dashboardActions";
import { kFormatter } from "../../../../../../utils";
import TopFilter from "../../../Dashboard/components/common/topFilter";
import WallList from "../../layout/common/WallList";
const LeaderbordSkeleton = React.lazy(() =>
  import("../../layout/skeleton/leaderbordSkeleton")
);

const AuthorpostsPopup = React.lazy(() =>
  import("../../layout/popup/authorpostsPopup")
);

 const LeaderBoard = (props) => {

  const { leaderBoard, loader, selectedWallFeed, isDashboard,  isTrial , showMore , hideShowMore } = props;
  const [show, setShow] = useState(false)

  const handlePopup = event => { 
    setShow(event)
    hideShowMore(event)
  }


  return (
   <>
    <Card className={`sw-card-custom gutter-b border-0 mb-0 ${isDashboard ? 'h-100' : 'card-shadow mb-4 an-leaderBoard-height'}`}>
   {isDashboard ?  '' :
     <SectionHeading title={'Leaderboard'} isTrial={isTrial} total={leaderBoard && Object.keys(leaderBoard).length ? leaderBoard.total : 0} selectedWallFeed={selectedWallFeed} handlePopup={handlePopup} />}
      <CardBody className="py-2 px-0">
        <Table responsive>
          <thead>
            <tr>
              <th style={{ width: "10%" }}>
                S No.
              </th>
              <th style={{ width: "25%" }}>
                Name
              </th>
              <th style={{ width: "15%" }}>
                Posts
              </th>
              <th style={{ width: "20%" }}>
                Recent Posts
              </th>
              <th style={{ width: "15%" }}></th>
            </tr>
          </thead>
          <tbody>


            { loader ? <Suspense fallback={<></>}> <LeaderbordSkeleton isDashboard={isDashboard}/> </Suspense> : leaderBoard && Object.keys(leaderBoard).length
              ? leaderBoard.data.map((data,index) => {
                  return (
                    <tr className="border-top">
                      <td>
                        <strong>{index+1}.</strong>
                      </td>
                      <td> 
                        <AuthorInfo 
                            picture={data.image} 
                            authorUsername={data.author.username} 
                            authorName={data.author.name} 
                            isIcon={data.network.icon} 
                            link={data.links}  
                            networkId={data.network.id} 
                          />
                      </td>
                      <td>
                        <strong>{kFormatter(Math.round(data.postCount))}</strong>
                      </td>
                      <td>
                        <MultiPostMedia isDashboard={isDashboard} postsData={data.postsData} /> 
                      </td>
                      <td>
                        <ConnectButton link={data.links} network={data.network.id}/>
                      </td>
                    </tr>
                  );
                })
              : 
              <>
              <NoData status={1} col={5}/>
              </>
             }
          </tbody>
        </Table>
      </CardBody>
     </Card>

   {leaderBoard && Object.keys(leaderBoard).length && leaderBoard.total>5 ? 
    <PopupModal size={'xl'} showHide={show || showMore} handlePopup={handlePopup} title={'Leader Board'}>
      <Suspense fallback={<></>}>
         <AuthorpostsPopup total={leaderBoard.total}  hideFollwers={1} moreData={isDashboard ? getwallRecordMostActiveUsersMoreDashboad : getwallRecordMostActiveUsersMore} {...props} />
       </Suspense>  
    </PopupModal> : null }
    </>    
  );
};

export default React.memo(LeaderBoard);