import React from "react";
import { FontAwesomeIcon } from "../../../../../CommonComponents/AssetsHelper";

const NetworkIcon = (props) => { 
  return (
    <> 
    <FontAwesomeIcon icon={props?.icon.includes("twitter") ? 'x-twitter' : props?.icon?.replace('fa-','')} iconClass={`${props?.icon == 'fa-rss' ? "" : 'fa-brands fab'} ${props.isDashboard ? props.isDashboard : ''}`} /> 
    </>
  );
};

export default React.memo(NetworkIcon);
