import AuthorInfo from "../../content/components/authorInfo";
import ShowMoreContent from "./showMoreContent";
import ConnectButton from "./connectButton";
import Media from "./media/Media";
import NetworkIcon from "./networkIcon";

  const SliderItem = ({ assetItem, assetKey, active, newPicture }) => {

    const newObject = {
      ...assetItem,
      id: assetItem.postId
  };

    return active ? (
      <>
        <div className="influential_contributors_custom_modal p-5 sw_theme">
          <div className="influential-data-list">
            <div className="row align-items-start">

            { ![1].includes(assetItem.type) ?  
              <div className="col-md-6 influential_contributors_img">
                 <div className="d-flex justify-content-center h-100 align-items-center">
                   <Media item={newObject}/>
                 </div>
              </div> : null }

              <div className={`influential_contributors_content col-md-${ ![1].includes(assetItem.type) ? '6' : '12'}`}>
                <div className="thumbnail-preview-content">
                  <div className="">
                    <div className="d-flex justify-content-between align-items-center mt-4">
                      <div className="d-flex align-items-center popup-author">
                        <AuthorInfo
                          picture={assetItem.authorImage}
                          authorUsername={assetItem.username}
                          date={""}
                          networkId={assetItem.networkId}
                          link={assetItem.link}
                          authorName={assetItem.name} 
                        />
                      </div>
                      <div className="poup-icon-font-size">
                        <NetworkIcon icon={assetItem.Network.icon}/>
                      </div>
                    </div>
                    <p className="popup-content mt-3"><ShowMoreContent
                        classes={"influ_content"}
                        content={assetItem.content}
                        idIndex={0}
                        line={3}
                      /></p>
                  </div>
                  <div className="">
                    <div className="conn_status connnected popup-connnected mb-3">
                      <ConnectButton link={assetItem.link} network={assetItem.Network.id} />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    ) : null;
  };

  export default SliderItem;
