import React from "react";
import { NoCameraImgPost } from "../../../../../../actions/api";
import { FontAwesomeIcon } from "../../../../../CommonComponents/AssetsHelper";
import { Image } from "react-bootstrap";

const PostMedia = (props) => {

  const { postId,content,mediaType,className,isSplide,postFile,multiPost } = props

  return (
    <div className="position-relative d-inline-block w-100 h-100">
      

      { mediaType==1 ?
         <div className="no-image-dis"> <span>{content}</span> </div>
          : (
        <>
          <Image
            id={`post_${postId}`}
            onError={(e) => (e.target.src = NoCameraImgPost)}
            src={!isSplide ? postFile : null}
            data-splide-lazy={isSplide ? postFile : null}
            className={`${className} ${multiPost ? "img-45" : ""}`}
          />

          {[3, 5].includes(mediaType) ? (
            <div className="play-media">
                <FontAwesomeIcon type={'solid'} icon={'circle-play'} iconClass={'text-white fs-2'} />
            </div>
          ) : null}
        </> 
      )}
    </div>
  );
};

export default React.memo(PostMedia);
